<template>
  <div class="contents">
    <mainLeft class=" animated fadeInLeft delay_0"></mainLeft>
    <div class="contents-right animated fadeInRight delay_0">
        <h1>{{an.h1}}</h1>
        <div class="guangao">中国医院管理案例评选（第二季）鸣锣！曾经果敢做，现在大声说。现金奖励恭候卓越案例</div>
        <p>{{an.text}}</p>
        <div v-for="item in this.an.children" :key="item.id">
          <h4>{{item.h4}}</h4>
          <p>{{item.p}}</p>
        </div>
        <span>娜乌西卡科技有限公司</span>
    </div>
  </div>
</template>

<script>
import mainLeft from "~/components/mainLeft.vue";
import { contents } from "~/data/index";

export default {
  data() {
    return {
      contents: contents,
      contentsData: {},
      an: {}
    };
  },
  created() {
    this.contentsData = this.$route.params.id;
    window.scrollTo(0, 0);

    function getData(id) {
      let tmp = null;
      contents.forEach(item => {
        if (item.id != id) {
          item.children.forEach(item => {
            if (item.id != id) {
              item.children.forEach(item => {
                if (item.id != id) {
                } else {
                  tmp = item;
                  return tmp;
                }
              });
            } else {
              tmp = item;
              return tmp;
            }
          });
        } else {
          tmp = item;
          return tmp;
        }
      });
      return tmp;
    }
    let test = getData(this.contentsData);
    this.an = test;
  },
  components: {
    mainLeft
  }
};
</script>

<style scoped lang="scss">
.contents-right {
  overflow: hidden;
  padding: 40px;
  background: #fff;
  margin-bottom: 30px;
  h1 {
    text-align: center;
    margin-bottom: 15px;
  }
  .guangao {
    background-color: rgb(255, 252, 239);
    border: 1px solid rgb(255, 204, 153);
    color: rgb(255, 153, 102);
    padding: 8px 3%;
    font-size: 12px;
    line-height: 150%;
    text-align: center;
    margin-bottom: 30px;
  }
  h4 {
    font-size: 18px;
    color: #202020;
    line-height: 175%;
    margin: 10px 0;
    font-weight: bold;
  }
  p {
    font-size: 16px;
    color: #202020;
    line-height: 175%;
    margin-top: 20px;
  }
  span {
    color: rgb(255, 153, 102);
    float: right;
    margin: 50px 0;
    font-size: 16px;
  }
}

.contents::after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}
</style>



