        text: '本文作者将从合同与需求分析的关系、需求调研、需求分析、需求定义、需求变更与需求补充、用户参与等几个角度来阐述基于标准产品实施开发的需求分析方法。',
        children: [{
            p: '有这么一个群体，需求分析工作在客户现场进行，而且已经有一款标准产品。项目组需要在标准产品的基础上基于用户需求进行个性化开发工作。我们把这种场景下面的需求分析叫做基于标准产品实施开发的需求分析。每当我们在实施此类型的需求分析工作时，需求分析人员常常限于合同约定的范围拒绝许多客户的需求，但同时又要兼顾甲方的要求和满意程度。这就到了考验实施开发项目组，尤其是需求分析人员的时候了。'
        }, {
            h4: '一、合同签订与需求分析',
            p: '严格意义上来说，合同签订与需求分析是不可区分的。只有事先了解到用户需求，才能准确把握用户的个性化做什么，做到什么程度。从而更好的评估工时确定合同价格。'
        }, {
            h4: '二、需求调研',
            p: '在项目确立后，就到了需求调研分析阶段。如第一点所说，合同规定的个性化需求只是一个框架，是大方向的需求。而实际某一个功能要做成什么样还需要在需求调研阶段明确下来。'
        }, {
            h4: '三、需求分析',
            p: '需求分析阶段首先要判断用户的需求是什么类型的。对于上面提到的合同范围内的个性化需求，我们就需要设计详细方案，并写入需求池，此类需求为最高优先级；对于合同范围内的标准产品可变通实现的需求，我们首先需要与客户沟通，此类需求就全靠嘴了，本质上说，此类需求最考验项目经理与需求分析师的综合能力。理想情况是此类需求可通过与用户沟通，实现不开发或者少开发。若与用户沟通失败，此类需求也可作为与用户争议第1类型需求的筹码（假如存在合同界定不清的情况）。其实主要取决于用户是否愿意接受标准产品的操作方式。如果用户愿意接受，我们就会可以将此类需求记录下来，暂时不做开发。如果用户不接受标准产品的操作方式，我们就需要设计详细方案，并纳入到需求池中，需求优先级低于合同范围内的个性化需求；对于两种合同范围外的个性化需求与标准产品可变通实现的需求，我们需要视与客户的沟通情况决定走需求变更或者下个版本实现。'
        }, {
            h4: '四、需求定义',
            p: '往往初步形成的是需求规格说明书初稿。如何写需求规格说明书，我们将在下一次讨论。需求规格说明书初稿形成之后，需求人员组织内部需求评审。由于基于标准产品项目实施的特殊性，本需求规格说明书的内部评审融入到需求规格说明书撰写过程，即，需求规格说明书本身就有研发参与，是否可实现，实现难度均已确认。所以需求规格说明书初稿形成后，研发同事整体浏览一遍，如无重大问题即可与用户开会讲解确认。'
        }, {
            h4: '五、需求变更与需求补充',
            p: '前文将需求分类四大类。其中，合同范围内的个性化需求。写入需求规格说明书，与用户确认后正常开发；合同范围内的标准产品可变通实现的需求，视与客户沟通情况决定是否开发；至于合同范围外的个性化需求与合同范围外的可变通实现的需求。前者视情况安排二期开发或者走需求变更，合同变更流程，由于时间跨度会比较长，一般是建议纳入二期开发计划。后者就尝试说服用户改变操作方式与操作习惯，暂时满足用户的基础需求。'
        }, {
            h4: '六、用户参与的重要意义',
            p: '总之，基于标准产品实施开发的需求分析，需求分析师可以从商务谈判，售前沟通开始介入。尽可能细化合同签订时的个性化需求清单，在与客户争议某类需求为合同范围外还是合同范围内时可以掌握一定的主动权。然后动员用户积极参与需求调研环节，深入了解用户的需求。最后就是在处理合同范围内标准产品可变通实现的需求时，与用户的沟通情况直接决定后续的开发量。'
        }]
